* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* font-family: 'Montserrat', sans-serif; */
  /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
  font-family: 'Crimson Text', serif;
  background-color: #FFF1E0;
  /* background-color: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  ); */



  color: rgb(75, 72, 72);
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  
  /* background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  ); */
  /* background-image: linear-gradient(
    -225deg,
    #ff0000 0%,
    #dfcfcf 48%,
    #3ff108 100%
  ); */
  background-image: linear-gradient(
    -225deg,
    /* #bc2df5 0%,
    #fcfcfc 48%,
    #38f8ff 100% */

    rgb(2, 36, 39),
    rgb(13, 118, 128),
    rgb(226, 226, 226)

  );


  color: #e2e2e2;
  font-size: 20px;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}
.coin-container {
    display: flex;
    justify-content: center;
    /* font-size: 20px; */
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    /* justify-content: start; */
    align-items: center;
    height: 80px;
    border-bottom: 1px solid rgb(13, 118, 128);
    width: 900px;
  }


  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
  
    min-width: 300px; 
    /* font-size: 80px; */
  }
  
  .coin h1 {
    font-size: 16px;
    width: 150px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
  }
  
  .coin-price {
    width: 110px;
  }
  
  .coin-volume {
    width: 155px;
  }
  
  .coin-marketcap {
    width: 230px;
  }
  
  .coin-percent {
    width: 100px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
.container {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

.heading {
    display: flex;
    justify-content: center;
    height: 100px;
    align-items: center;
}
  
.graph_container {
    display: flex;
    justify-content: center;
    height: 200px;

  }
  
  .y_label {
    display: flex;
    /* flex-direction: row; */
    justify-content: center;
    width: 20px;
    align-items: center;

  }
  
  .graph {
    display: flex;
    /* flex-direction: row; */
    justify-content: center;

    margin-left: auto;
    margin-right: auto;
    width: 40%;
    width: 800px;
    height: 450px;

  }
  
  
  .info {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 60%;
    padding-top: 35px;
    text-align: justify;
    line-height: 1.6;
  
  } 
  
 
  
  .h1 {

  }







































